import React,{ useState,useEffect,useRef} from 'react';
import "./path.css";
import PaperImage from "./paper.png";

function useForceUpdate(){
  const [value, setValue] = useState(0); // integer state
  return () => setValue(value => value + 1); // update the state to force render
}

const LearningPath = () => {
  const [Circles, setCircles] = useState([]);
  const forceUpdate =useForceUpdate()

  useEffect(() => {
    let oldScroll=window.scrollY
    setInterval(forceUpdate,10)
    let plane = document.getElementById("paper-plane")
    let parent =plane.offsetParent
    let rect = plane.getBoundingClientRect()
    
    let y = ( rect.y-parent.getBoundingClientRect().y)+(rect.height/2)
    let x = plane.getBoundingClientRect().x +(rect.width/2)

    let first={
      x:x,
      y:y,
      r:2
    }
    setCircles([first])
    window.onscroll=()=>{
      if(window.scrollY>oldScroll){
        oldScroll=window.scrollY;
        rect = plane.getBoundingClientRect()
        y = ( rect.y-parent.getBoundingClientRect().y)
        y+= (y/7)
        x = plane.getBoundingClientRect().x +(rect.width/2)
        let last = Circles.length===0?first: Circles[Circles.length-1]
        
        let distance = Math.pow((y-last.y),2) + Math.pow((x-last.x),2)
        if(distance>=1000){
          let c = Circles
          c.push({
            x:x,
            y:y,
            r:2
          })
          setCircles(c)
        }
      }

    }
    
  }, []);
  return (
    <div id="trigger-element" className="paper-plane-animation">
      <img
        id={"paper-plane"}
        className="paper-plane"
        src={PaperImage}
        alt="paperplane"
      />
      <svg id="eNGgzVPcxCn1" width="{540}" height="{2100}" xmlns="http://www.w3.org/2000/svg" xmlnsxlink="http://www.w3.org/1999/xlink" viewBox="0 0 540 2100" shaperendering="geometricPrecision" textrendering="geometricPrecision">
        {Circles.map((circle)=>{
          return <circle cx={circle.x} cy={circle.y} r={circle.r} style={{fill:"#0000ff"}}/>
        })}
      </svg>

    </div>
  );
};

export default LearningPath;

const Path = (x, y) => {
  let ypercent = 700 / 100;
  let xpercent = window.innerWidth / 100;

  return {
    x: x * xpercent,
    y: y * ypercent,
    opacity: 1,
  };
};
